import { useEffect, useState } from "react";

const Upload = () => {
    const [ profilePic, setProfilePic ] = useState(null);

    useEffect( () => {
        fetch("/api/get-profile-pic")
            .then( (res) => res.json() )
            .then( (data) => {
                setProfilePic(data.profilePic);
            })
        ;
    }, [] );

    const [ driverID, setDriverID ] = useState(null);
    const [ driversLicensePic, setDriversLicensePic ] = useState(null);

    useEffect( () => {
        fetch("/api/get-pii-drivers-license")
            .then( (res) => res.json() )
            .then( (data) => {
                setDriverID(data.driversLicense);
                setDriversLicensePic(data.driversLicensePic);
            })
        ;
    }, [] );

    const handleSubmit = (e) => {
        e.preventDefault();

        const uploadForm = document.getElementById("upload-form");
        const formData = new FormData(uploadForm);

        fetch("/api/upload-profile-pic", {
            method: "POST",
            body: formData
        }).then( (res) => res.json() )
        .then( (data) => {
            alert(data.status);
        });
    }

    const handleSubmitDriversLicense = (e) => {
        e.preventDefault();

        const uploadForm = document.getElementById("upload-form");
        const formData = new FormData(uploadForm);

        fetch("/api/upload-pii-drivers-license", {
            method: "POST",
            body: formData
        }).then( (res) => res.json() )
        .then( (data) => {
            alert(data.status);
        });
    }

    return(
        <div className="upload m-5">
            <form id="upload-form" encType="multipart/form-data">
                <div className="pii-drivers-license mb-5">
                    <input type="text" name="drivers_license" />
                    <br />
                    <input type="file" name="drivers-license-pic" accept=".jpeg,.jpg,.png" multiple></input>

                    <button onClick={handleSubmitDriversLicense}>Upload driver's license</button>

                    <br /><br />
                    <span>ID: {driverID}</span>
                    <br />
                    <img src={driversLicensePic} alt="" />
                </div>

                <input type="file" name="photo" accept=".jpeg,.jpg,.png" multiple></input>
                <button onClick={handleSubmit}>Upload</button>

                <br /><br />
                <img src={profilePic} alt="" />
            </form>
        </div>
    );
}

export default Upload;